body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #020617;
  overflow-x: hidden;
}

/* Prevent flash of unstyled content */
#root {
  min-height: 100vh;
  background-color: #020617;
  // padding-bottom: 100px;
}

.admin-page {
  background-color: #f9fafb !important;
  #root { background: #f9fafb !important;}

}

.lakinh-page {
   padding-bottom: 0px !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.arrow-up {
  width: 0;
  height: 0;
  border-left: 16px solid transparent;
  border-right: 16px solid transparent;
  border-bottom: 16px solid #fff;
}

/* CSS Variables for dynamic font sizing */
:root {
  --font-size-base: 16px;
  --font-size-small: 14px;
  --font-size-medium: 16px;
  --font-size-large: 18px;
  --font-size-xlarge: 20px;
  
  --line-height-base: 1.625;
  --line-height-small: 1.4;
  --line-height-medium: 1.625;
  --line-height-large: 1.725;
  --line-height-xlarge: 1.8;
  
  --heading-size-h1: 2rem;
  --heading-size-h2: 1.5rem;
  --heading-size-h3: 1.25rem;
  --heading-size-h4: 1.125rem;
  
  --paragraph-margin: 1rem;
}

/* Font size classes for body - Updated with CSS variables */
.client-page.fontSize14 {
  --font-size-base: 14px;
  --line-height-base: 1.4;
  --heading-size-h1: 1.75rem;
  --heading-size-h2: 1.25rem;
  --heading-size-h3: 1.125rem;
  --heading-size-h4: 1rem;
  --paragraph-margin: 0.75rem;
  
  .article-content { 
    font-size: var(--font-size-base) !important;
    h1 { font-size: var(--heading-size-h1) !important; }
    h2 { font-size: var(--heading-size-h2) !important; }
    h3 { font-size: var(--heading-size-h3) !important; }
    h4 { font-size: var(--heading-size-h4) !important; }
    p { 
      margin-bottom: var(--paragraph-margin) !important;
      line-height: var(--line-height-base) !important;
    }
  }
  .leading-relaxed { line-height: var(--line-height-base) !important; }
  .text-3xl { font-size: 1.5rem !important; }
  .text-2xl { font-size: 1.25rem !important; }
  .text-xl { font-size: 1.125rem !important; }
  .text-lg { font-size: 1rem !important; }
  .text-base { font-size: 0.875rem !important; }
  .text-sm { font-size: 0.75rem !important; }
  .text-xs { font-size: 0.625rem !important; }
}

