#root { text-align: center;}

.has-gradient-top {
  position: relative;
  &:before {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-image: linear-gradient(to bottom, #020617, transparent);
    height: 80px;
    z-index: 30;
    position: fixed;
  }
}

.coverGrid {
  background-image: url('./assets/cover-grid.png');
  background-size: 100%;
  background-position: center bottom;
  background-repeat: no-repeat;
  height: 260px;
  @media (max-width: 768px) {
    height: 90px;
  }
}

