// Button styles
.button-style {
  // background-image: linear-gradient(90deg, #08bc96, #3ff496);
  // border: 1px solid #00ffc0;
  // color: #020617;
  background: linear-gradient(45deg, rgb(0, 255, 192), rgb(126, 100, 221));
