// Background gradients
.bg-gradient {
  &-to-r {
    background-image: linear-gradient(90deg, #08bc96, #3ff496);
    color: #020617;
  }
  
  &-to-l {
    background-image: linear-gradient(90deg, #08bc96, #3ff496);
  }
  
  &-to-r-2 {
    background-image: linear-gradient(90deg, #3808bc, #6f3ff4);
    color: #fff;
  }
}

// Box styles
.box-style {
  background-color: transparent !important;
  border: 0px solid #00ffc0;
  background-image: linear-gradient(-45deg, rgba(0, 255, 192, 0.35) 0px, rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 0) 70%, rgb(126, 100, 221) 100%);
  padding: 2px !important;
  border-radius: 10px;
  
  &-white {
    background-color: transparent !important;
    border: 0px solid #00ffc0;
    background-image: linear-gradient(-45deg, rgba(255, 255, 255, 0.15) 0px, rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 0) 70%, rgba(255, 255, 255, 0.15) 100%);
    padding: 10px;
    border-radius: 10px;
  }
  
  &-in {
    background-color: #101314;
    padding: 16px;
    border-radius: 10px;
    color: #fff;
  }
}

.box-in-box {
  border: 1px solid rgba(255, 255, 255, 0.5);
  box-shadow: 0 6px 22px 0 #002e29, inset 0 -8px 20px 0 rgba(0, 255, 192, .15);
  color: hsla(0, 0%, 100%, 0.9);
  background-color: transparent;
  
  &-2 {
    border: 1px solid rgba(255, 255, 255, 0.1);
    color: hsla(0, 0%, 100%, 0.9);
    background-color: #101314;
  }
}

// Line styles
.line-style {
  width: 100%;
  height: 1px;
  margin: 10px 5px;
  box-shadow: 0 1px 1px 0 rgba(255, 255, 255, 0.15);
  background-image: linear-gradient(-45deg, rgba(0, 255, 192, 0.35) 0px, rgba(0, 255, 192, 0.35) 30%, rgba(126, 100, 221, 0.35) 70%, rgb(126, 100, 221) 100%);
  
  &-camera {
    width: 100%;
    height: 4px;
    margin: 10px 5px;
    box-shadow: 0 0 20px 5px #6366f1, 0 0 40px 10px #818cf8;
    animation: glowIndigo 1.5s infinite alternate, animateLineInfinite 1.5s infinite alternate;
    background-image: linear-gradient(45deg, rgba(0, 255, 192, 1) 0px, rgba(0, 255, 192, 1) 30%, rgba(126, 100, 221, 1) 70%, rgb(126, 100, 221) 100%);
    background-size: 200% 200%;
    background-position: 0% 50%;
    background-repeat: no-repeat;
  }
  
  &-2 {
    width: 100%;
    height: 1px;
    margin: 10px 5px;
    box-shadow: 0 1px 1px 0 rgba(255, 255, 255, 0.15);
    background-image: linear-gradient(45deg, rgba(0, 255, 192, 0.35) 0px, rgba(0, 255, 192, 0.35) 30%, rgba(126, 100, 221, 0.35) 70%, rgb(126, 100, 221) 100%);
  }
}

// Animations
.animation-nav {
  animation: animateLineInfinite 1.5s infinite alternate;
  background-image: linear-gradient(45deg, rgba(0, 255, 192, 1) 0px, rgba(0, 255, 192, 1) 30%, rgb(126, 100, 221) 70%, rgb(126, 100, 221) 100%);
  background-size: 200% 200%;
  padding: 2px;
}

.fade-in {
  animation: fadeIn 0.8s ease-in-out;
  animation-fill-mode: both;
}

// Glow effects
.glow {
  &-pink {
    box-shadow: 0 0 10px 2px #ec4899, 0 0 20px 5px #f472b6;
    animation: glowPink 1.5s infinite alternate;
  }
  
  &-indigo {
    box-shadow: 0 0 20px 5px #6366f1, 0 0 40px 10px #818cf8;
    animation: glowIndigo 1.5s infinite alternate;
  }
  
  &-green {
    box-shadow: 0 0 20px 5px #34d399, 0 0 40px 10px #10b981;
    animation: glowGreen 1.5s infinite alternate;
  }
  
  &-blue {
    box-shadow: 0 0 20px 5px #60a5fa, 0 0 40px 10px #22d3ee;
    animation: glowBlue 1.5s infinite alternate;
  }
}

// Keyframes
@keyframes fadeIn {
  from { 
    opacity: 0; 
    transform: translateY(10px);
  }
  to { 
    opacity: 1; 
    transform: translateY(0);
  }
}

// Smooth app transition
.app-transition-enter {
  opacity: 0;
  transform: translateY(20px);
}

.app-transition-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: all 600ms ease-out;
}

@keyframes glowPink {
  0% { box-shadow: 0 0 10px 2px rgba(236, 72, 153, 0.5), 0 0 20px 4px rgba(244, 114, 182, 0.5); }
  100% { box-shadow: 0 0 30px 10px rgba(236, 72, 153, 0.5), 0 0 60px 20px rgba(244, 114, 182, 0.5); }
}

@keyframes glowIndigo {
  0% { box-shadow: 0 0 10px 2px rgba(99, 102, 241, 0.5), 0 0 20px 4px rgba(129, 140, 248, 0.5); }
  100% { box-shadow: 0 0 30px 10px rgba(99, 102, 241, 0.5), 0 0 60px 20px rgba(129, 140, 248, 0.5); }
}

@keyframes glowGreen {
  0% { box-shadow: 0 0 10px 2px rgba(74, 222, 128, 0.5), 0 0 20px 4px rgba(34, 211, 238, 0.5); }
  100% { box-shadow: 0 0 30px 10px rgba(74, 222, 128, 0.5), 0 0 60px 20px rgba(34, 211, 238, 0.5); }
