@keyframes glowBlue {
  0% { box-shadow: 0 0 10px 2px rgba(96, 165, 250, 0.5), 0 0 20px 4px rgba(34, 211, 238, 0.5); }
  100% { box-shadow: 0 0 30px 10px rgba(96, 165, 250, 0.5), 0 0 60px 20px rgba(34, 211, 238, 0.5); }
}

@keyframes glowGolden {
  0% { box-shadow: 0 0 10px 2px rgba(165, 93, 7, 0.5), 0 0 20px 4px rgba(254, 241, 162, 0.5); }
  100% { box-shadow: 0 0 30px 10px rgba(165, 93, 7, 0.5), 0 0 60px 20px rgba(254, 241, 162, 0.5); }
}

@keyframes animateLineInfinite {
  0% { background-position: 0% 50%; }
  100% { background-position: 100% 50%; }
}

// Loading screen styles
.loading-screen-enter {
  opacity: 0;
}

.loading-screen-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in-out;
}

.loading-screen-exit {
  opacity: 1;
}

.loading-screen-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in-out;
}

// Loading screen shimmer effect
@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.loading-shimmer {
  position: relative;
  overflow: hidden;
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    animation: shimmer 2s infinite;
  }
} 

.title-style {
  font-weight: bold;
  background: linear-gradient(45deg, #ff6666, #ffb366, #ffff66, #b3ff66, #66ff66, #66ffb3, #66ffff, #66b3ff, #6666ff, #b366ff, #ff66ff, #ff66b3, #ff6666);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #00ffc0;
  font-size: 24px;
}

