// Daily Rewards Balloon Styles
.daily-rewards-balloon {
  animation: balloon-float 3s ease-in-out infinite;
}

@keyframes balloon-float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-10px) rotate(2deg);
  }
}

.balloon-sparkle {
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes sparkle {
  0%, 100% {
    opacity: 0.3;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}

.balloon-pulse {
  animation: balloon-pulse 1.5s ease-in-out infinite;
}

@keyframes balloon-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 243, 74, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(255, 243, 74, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 243, 74, 0);
  }
}