/* 
  🚀 ĐỒNG BỘ LOADING SCREEN CSS
  - Đồng bộ với HTML initial loading screen
  - Sử dụng cùng animations và styles
  - Responsive và accessible
*/

.loading-screen {
  min-height: 100vh;
  min-height: 100dvh; /* For better mobile support */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

/* Golden theme specific styles */
.theme-golden .loading-screen {
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 50%, #404040 100%) !important;
}

.theme-golden .loading-screen h1 {
  background: linear-gradient(45deg, #fef1a2 0px, #fef1a2 30%, #a54e07 70%, #a54e07 100%) !important;
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent !important;
  background-clip: text !important;
}

.theme-golden .loading-screen p {
  color: #fef1a2 !important;
}

/* Golden loading animation enhancements */
.theme-golden .loading-screen .loading-shimmer::after {
  background: linear-gradient(90deg, transparent, rgba(254, 241, 162, 0.3), transparent) !important;
}

/* Responsive adjustments */
@media (max-width: 640px) {
  .loading-screen h1 {
    font-size: 2rem !important;
  }
  
  .loading-screen p {
    font-size: 1rem !important;
  }
}

/* Prefers reduced motion */
@media (prefers-reduced-motion: reduce) {
  .loading-screen * {
    animation: none !important;
  }
} 